@import '../../../Common/sass/variables.scss';
.remoteWrapper{
    min-height: 250px;
    background-color: $backgroundSecondColor;
    display: flex;
    align-items: center;
    justify-content: center;
    .remote{
        min-height: 150px;
        width: 818px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        .hireButton{
            width: 150px;
            color: $smallTextColor;
            background-color: transparent;
            height: 40px;
            border-radius: 20px;
            border: 1px solid $titleColor;
        }
        .hireButton:hover{
            background-color: $smallTextColor;
            border: none;
            color: $secondaryBackgroundColor;
            transition: .5s;
        }
        .aboutWork{
            color: $smallTextColor;
        }
    }
}


@media (max-width: 860px ) {
    .remote{
        border: none;
    }
}
@media (max-width: 640px) {
    .remote{
        border: none;
    }
}