@import '../../Common/sass/variables.scss';
.title{
  color: $titleColor;
  position: relative;
  padding: 0;
  margin: 0;
}
.title::after {
  content: '';
  background-color: $secondaryColor;
  width: 70px;
  position: absolute;
  height: 1px;
  bottom: -10px;
  margin: auto;
  left: 0;
  right: 0;
}